<script>
import { getData, opthionList } from "common/utils/public_function/commonUtils";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "aftersaleThreeregisterDefault",
  data() {
    return {
      authority: {},
      dialogFormVisibleSee: false, //修改弹框
      loading: true,
      purchaseprice: true, //int 初始化显示数据列表展示
      planAdds: false, //增加 修改采购计划列表
      examineprice: false, //查看采购计划详情
      lock: 0,
      params: {},
      paramsChange: {},
      searchParams: {}, //参数传递值
      goPage: 1, //默认搜索页面
      pageSize: 30, //默认表格size
      loading: true,
      per_page: [30, 50, 100, 500],
      TableData: {
        tableData: [],
        total: 10
      },
      timeValue: [],
      supplierList: {
        options: []
      },
      auditStatus: {
        options: []
      },
      analyzeList: {
        options: []
      },
      goodsList: [], //商品下拉
      SelesTomer: {
        options: []
      }, //客户列表
      multipleSelection: [], //已选项

      dialogFormVisible: false,
      newresult: []
    };
  },
  computed: {
    ...mapGetters(["threeregisterDefault_showGet"])
  },
  methods: {
    ...mapActions([
      "threeregisterAdd_show",
      "threeregisterDefault_show",
      "threeregisterChange_show"
    ]),
    changeType(val) {
      if (val < 1) {
        this.$message({
          message: "返台数量只能大于等于1",
          type: "warning"
        });
        this.paramsChange.goods_num = 1;
      } else if (val > 0) {
        this.paramsChange.goods_num = Math.round(val);
      }
    },
    companyChange(val) {
      if (val) {
        this.params.company_name = this.companyList.filter(
          obj => obj.company_id === val
        )[0].company_name;
      }
      this.$set(this.params, "supplier_id", "");
      this.$set(this.params, "department_id", ""); // 初始化部门下拉选中值
      this.$set(this.params, "customer_id", ""); // 初始化客户名称下拉选中值
    },
    deparmentChange(val) {
      if (val) {
        this.params.department_name = this.departmentList.filter(
          obj => obj.department_id === val
        )[0].department_name;
      }
    },
    customerListChange(val) {
      if (val) {
        this.params.customer_name = this.customerList.filter(
          obj => obj.customer_id === val
        )[0].customer_name;
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    examineFun() {
      //审核
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要审核的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].register_goods_id);
      }
      var rebateArr = addr.toString();
      var funcan = {
        examineID: "register_goods_id",
        eachData: {
          register_goods_id: rebateArr
        },
        examineApi: "RegisterAuditorPass",
        list: {
          UrlApi: "getRegisterList",
          data: this.TableData,
          esData: this.params
        }
      };
      this.$common.examineFun(funcan);
    },
    examineFunF() {
      //弃审
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要弃审的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].register_goods_id);
      }
      var rebateArr = addr.toString();
      let obj = {
        register_goods_id: rebateArr
      };
      this.$axMethod.RegisterReturnAuditorPass(obj).then(response => {
        if (response.data.code == 200) {
          this.$message({
            message: response.data.data,
            type: "success"
          });
          this.$common.initData("getRegisterList", this.TableData);
        }
      });
    },
    abolishFun() {
      //作废
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要作废的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].register_goods_id);
      }
      var rebateArr = addr.toString();
      var funcan = {
        examineID: "register_goods_id",
        eachData: {
          register_goods_id: rebateArr
        },
        examineApi: "RegisterAuditorInvalid",
        list: {
          UrlApi: "getRegisterList",
          data: this.TableData,
          esData: this.params
        }
      };
      this.$common.examineFun(funcan);
    },
    change(val) {
      //选择的日期
      this.params.start_time = val[0];
      this.params.end_time = val[1];
    },
    handleEdit(scope, row) {
      this.dialogFormVisibleSee = true;
      this.$axMethod
        .getRegisterInfo({
          register_goods_id: row.register_goods_id
        })
        .then(response => {
          this.paramsChange = response.data.data;
        });
    },
    submitChange() {
      //修改确定方法
      this.$axMethod.editRegister(this.paramsChange).then(response => {
        if (response.data.code == 200) {
          this.$message({
            message: response.data.data,
            type: "success"
          });
          this.dialogFormVisibleSee = false;
        }
        this.dialogFormVisibleSee = false;
        getData(this.searchParams);
      });
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.timeValue = [];
      this.$set(this, "params", {});
      this.$set(this, "searchParams", {
        params: {},
        TableData: this.TableData, //列表数据
        API: "getRegisterList", //接口名
        vm: this //传递的this
      });
      getData(this.searchParams);
      // wz cache
      this.$router.push({ path: "/aftersale/threeregister" });

      this.threeregisterDefault_show({
        params: {},
        goPage: this.goPage,
        pageSize: this.pageSize
      });
    },
    examine(scope, row) {
      //查看采购计划详情显示与隐藏的方法
      this.planAdds = false;
      this.purchaseprice = false;
      this.examineprice = true;
      this.lock = row.instorage_id;
    },
    purchase(msg) {
      this.purchaseprice = msg.purchaseprice;
      this.planAdds = msg.planAdds;

      this.examineprice = msg.examineprice;
      this.$common.initData("getRegisterList", this.TableData);
    },
    planAdd() {
      this.$router.push({ path: "/aftersale/threeregister/threeregister_add" });
      this.threeregisterAdd_show({
        goPage: this.goPage,
        pageSize: this.pageSize,
        params: this.params
      });
    },
    onSearch() {
      if (this.timeValue) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      Object.assign(this.searchParams.params, this.params, {
        page: this.goPage
      });
      getData(this.searchParams);
      // wz cache
      this.$router.push({ path: "/aftersale/threeregister" });
      this.threeregisterDefault_show({
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize
      });
    },
    //删除
    deletes() {
      var deledata = {
        deleID: "register_goods_id",
        deleSelect: this.multipleSelection,
        deleApi: "delRegister",
        list: {
          UrlApi: "getRegisterList",
          data: this.TableData
        }
      };
      this.$common.deletes(deledata, "删除", this);
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1
      });
      getData(this.searchParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val
      });
      getData(this.searchParams);
    }
  },
  mounted() {
    // wz cache
    let wz_search_params = {};
    this.authority = JSON.parse(localStorage.getItem("authority"));
    // getCompanyListCommon(this); //获取组织
    if (
      this.threeregisterDefault_showGet &&
      this.threeregisterDefault_showGet.params
    ) {
      if (this.threeregisterDefault_showGet.params.start_time) {
        const {
          start_time,
          end_time
        } = this.threeregisterDefault_showGet.params;
        this.timeValue = this.timeValue.concat([start_time, end_time]);
      }
      this.$set(this, "params", this.threeregisterDefault_showGet.params);
      wz_search_params = this.params;
      opthionList(this, this.threeregisterDefault_showGet.params);
    }
    if (
      this.threeregisterDefault_showGet &&
      this.threeregisterDefault_showGet.goPage
    ) {
      this.params.page = this.threeregisterDefault_showGet.goPage;
      this.params.per_page = this.threeregisterDefault_showGet.pageSize;
    }

    this.$set(this, "searchParams", {
      params: wz_search_params, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getRegisterList", //接口名
      vm: this //传递的this
    });
    getData(this.searchParams);
  }
};
</script>

